<template>
  <div>
    <p class="information"></p>
    <div>
      <div id="popBox">
        <el-button type="primary" @click="visible1=true" v-if="!isUnionOwner"></el-button>
        <el-button type="warning" style="padding: 10px 15px 10px 32px;position: relative">
          <img src="~assets/images/Videos.png" style="width: 17px;position: absolute;top: 8px;left: 7px;"> 
        </el-button>
        <p class="fr" style="color:#20A0FF;cursor:pointer;" @click="visible2=true">?</p>
      </div>
      <div style="margin-top: 25px" class="second">
        <el-table :data="tableData">
          <el-table-column prop="member.enterpriseName" label="">
          </el-table-column>
          <el-table-column prop="memberOut.confirmOutTime" label="">
          </el-table-column>
          <el-table-column prop="memberOut.type" label="">
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              {{ scope.row.periodDay }} 
            </template>
          </el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <el-button size="small" v-if="isUnionOwner && scope.row.memberOut.type === ''" @click="cancel(scope)"></el-button>
              <el-button size="small" v-if="!isUnionOwner && memberId === scope.row.member.id && scope.row.memberOut.type === ''" @click="cancel(scope)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="footer">
          <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalAll" v-if="tableData.length>0">
          </el-pagination>
        </div>
      </div>
      <!--   -->
      <div class="RefundAu">
        <el-dialog title="" :visible.sync="visible1" size="tiny" @close="resetData">
          <hr>
          <div class="main">
            <el-form ref="form" :model="form" :rules="rules" label-width="80px">
              <el-form-item label=":" prop="outReason">
                <el-input type="textarea" v-model="form.outReason" :rows="3" placeholder="">
                </el-input>
              </el-form-item>
            </el-form>
            <p>
              15
            </p>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="outConfirm('form')"> </el-button>
            <el-button @click="visible1=false"> </el-button>
          </span>
        </el-dialog>
      </div>
      <!--  -->
      <div class="tuimeng">
        <el-dialog title="" :visible.sync="visible2" size="tiny">
          <hr>
          <div class="model_12">
            <p>1. </p>
            <p>2.  </p>
            <p>3. </p>
            <p>4. </p>
            <p>5. </p>
            <p>6. </p>
            <p>7. 15</p>
            <p>8. </p>
            <p>&nbsp;&nbsp;&nbsp;&nbsp;15</p>
            <p>9. </p>
            <p>10. </p>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="visible2=false"> </el-button>
          </span>
        </el-dialog>
      </div>
      <!--   -->
      <div class="model_12">
        <el-dialog title="" :visible.sync="visible3" size="tiny">
          <p> {{ enterpriseName }} </p>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="confirm3"></el-button>
            <el-button @click="visible3=false"></el-button>
          </span>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import $http from '@/utils/http.js';
import { timeFilter } from '@/utils/filter.js';
import { reasonPass } from '@/utils/validator.js';
export default {
  name: 'union-quit-transition',
  data() {
    return {
      visible1: false,
      visible2: false,
      tableData: [],
      currentPage: 1,
      totalAll: '',
      form: {},
      rules: {
        outReason: [{ validator: reasonPass, trigger: 'blur' }]
      },
      enterpriseName: '',
      visible3: false,
      outId: ''
    };
  },
  computed: {
    isUnionOwner() {
      return this.$store.state.isUnionOwner;
    },
    unionId() {
      return this.$store.state.unionId;
    },
    memberId() {
      return this.$store.state.memberId;
    }
  },
  mounted: function() {
    this.init();
  },
  methods: {
    init() {
      this.currentPage = 1;
      this.getTableData();
    },
    getTableData() {
      $http
        .get(`/unionMemberOut/unionId/${this.unionId}/period/page?current=${this.currentPage}`)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records || [];
            this.tableData.forEach((v, i) => {
              v.memberOut.confirmOutTime = timeFilter(v.memberOut.confirmOutTime);
              v.memberOut.type === 1 ? (v.memberOut.type = '') : (v.memberOut.type = '');
            });
            this.totalAll = res.data.data.total;
          } else {
            this.tableData = [];
            this.totalAll = 0;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getTableData();
    },
    // 
    outConfirm(formName) {
      let url = `/unionMemberOut/unionId/${this.unionId}`;
      let data = this.form.outReason;
      this.$refs[formName].validate(valid => {
        if (valid) {
          $http
            .post(url, data)
            .then(res => {
              if (res.data.success) {
                this.visible1 = false;
                this.$message({ showClose: true, message: '', type: 'success', duration: 5000 });
                eventBus.$emit('newOutApply');
              }
            })
            .catch(err => {
              this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
            });
        } else {
          return false;
        }
      });
    },
    // 
    resetData() {
      this.form.outReason = '';
    },
    // 
    cancel(scope) {
      this.enterpriseName = scope.row.member.enterpriseName;
      this.outId = scope.row.memberOut.id;
      this.visible3 = true;
    },
    // 
    confirm3() {
      $http
        .del(`/unionMemberOut/${this.outId}/unionId/${this.unionId}`)
        .then(res => {
          if (res.data.success) {
            this.$message({ showClose: true, message: '', type: 'success', duration: 5000 });
            this.init();
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    }
  }
};
</script>



